html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.container {
  position: relative;
}

.header_block {
  background-color: #303038;
  padding: 0px;
  margin: 0px;
}

.header_top {
  height: 50px;
}

.header_top_social {
  height: 50px;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
}

.header_top_social_left {
  color: white;
  position: relative;
  padding-left: 10px;
  font-weight: 400;
  font-size: 0.9375rem;
  line-height: 1.46667;
}

.header_top_social_left span {
  display: inline-block;
  margin-left: 5px;
}

.header_top_social_right li {
  display: inline-block;
}

.header_top_social_right li a {
  display: inline-block;
  padding: 5px;
  color: #0a6db7;
  font-size: 1.25rem;
}

.header_top_social_right li a:hover {
  color: #ffffff;
}

.top_line {
  background-color: #ffffff;
  width: 100%;
  z-index: 100;
  padding: 20px 0;
}

.top_line_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top_line_logo {
  color: #fff;
}

.top_line_menu {
  padding: 0;
  margin: 0;
}

.top_line_block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top_call_box {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-start;
}

.top_call_box_item {
  /*  width: 33.33333%; */
  min-height: 42px;
  padding-left: 3px;
  padding-right: 3px;
  margin-right: 20px;
}

.top_call_box_item:last-child {
  margin-right: 0;
}

.tci_locate {
  display: inline-block;
  vertical-align: top;
  font-size: 2.5rem;
  color: #0a6db7;
}

.tci_info {
  display: inline-block;
}

.tci_title, .tci_descr {
  white-space: pre;
}

.tci_title {
  font-family: "LatoBold", sans-serif;
  font-size: 0.9375rem;
  line-height: 1.46667;
  color: #242424;
}

.tci_descr {
  font-size: 0.875rem;
  line-height: 1.5;
  color: #505050;
}

/* .top_line {margin-bottom: 80px;} */
.mobile_gamburger {
  display: none;
}

.top_menu {
